import React, { useState } from "react";
import axios from "axios";
import "./App.css";

const App = () => {
  const [message, setMessage] = useState("Waiting...");
  const [roll, setRoll] = useState({
    rollVal: [],
  });
  const [result, setResult] = useState([]);

  const trigger = (e) => {
    setResult([]);
    setMessage("Waiting...");
    if (e.target.value == "") {
      document.getElementById("display").style.display = "none";
    } else {
      document.getElementById("display").style.display = "";
      const regex = /([^\,\s]+)/g;
      const match = e.target.value.match(regex);
      console.log(match);
      setRoll({ rollVal: match });
    }
  };

  const findResult = () => {
    console.log(roll);
    setMessage("Fetching...");

    axios
      .post("/students", roll)
      .then((res) => {
        console.log(res.data.result);
        setResult(res.data.result);
        setRoll({ rollVal: res.data.roll });
      })
      .catch((e) => console.log(e));
  };

  return (
    <>
      <div className="design mb-3 fonts">
        <h2>Roll Result Finder</h2>
      </div>
      <div className="row justify-content-center mb-2 mt-1">
        <div className="col-6">
          <div className="form-group mb-2">
            <input
              type="text"
              className="form-control"
              placeholder="Enter Roll No"
              onChange={trigger}
            />
          </div>
        </div>
      </div>
      <div className="mb-2 design">
        <button onClick={findResult} className="btn btn-success">
          Find
        </button>
      </div>
      <table className="table table-striped table-hover table-bordered">
        <thead>
          <tr>
            <th scope="col">S.No</th>
            <th scope="col">Roll</th>
            <th scope="col">Result</th>
          </tr>
        </thead>
        <tbody id="display">
          {roll.rollVal.map((val, index) => (
            <tr key={index + 1}>
              <th scope="row">{index + 1}</th>
              <td>{val}</td>
              <td className="message">{result[index] || message}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </>
  );
};

export default App;
